import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';




const Navbar = ({titel}) => {
        return (
            <nav className="navbar bg-primary">
                <h1>
                {titel}
                </h1>
           
                <ul>
                    <li>
                        <Link to="/">Home</Link>
                    </li>
                    <li>
                        <Link to="/About">About</Link>
                    </li>
                </ul> 
            </nav>
        );
    };

 Navbar.defaultProps = {
    titel: 'Github Finder'
    
};

Navbar.propTypes = {
    titel: PropTypes.string.isRequired,
};

export default Navbar
